@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700');

body {
    width: 100%;
    min-height: 100vh;
    font-weight: 300;
    margin: 0;
    background: rgb(240, 240, 240);
    background: -webkit-gradient(linear, left top, right bottom, from(#4CA1AF), to(#C4E0E5));
    background: -webkit-linear-gradient(top left, #4CA1AF, #C4E0E5);
    background: -o-linear-gradient(top left, #4CA1AF, #C4E0E5);
    background: linear-gradient(to bottom right, #4CA1AF, #C4E0E5);
    padding: 10px 15px 80px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


